<script>
import { defineComponent } from 'vue'
import { toPrice } from '@plugins/money'

export default defineComponent({
    name: 'ProcedureBills',
    props: {
        items: {
            type: Array,
            default: () => [],
        },
        permissions: {
            type: Array,
            default: () => [],
        },
        stores: {
            type: Array,
            default: () => [],
        },
        invoice: {
            type: Object,
            required: true,
        },
        visitRecord: {
            type: Object,
            required: true,
        },
    },

    data () {
        return {

            fields: [
                {
                    name: '__slot:date',
                    title: 'DATE',
                    titleClass: ' text-end pe-3',
                    dataClass: ' text-end pe-3',
                    width: '8%',
                },
                {
                    name: '__slot:description',
                    title: 'DESCRIPTION',
                    titleClass: 'ps-3',
                    dataClass: 'ps-3',
                    width: '45%',
                },
                {
                    name: 'unit_price',
                    title: 'PRICE',
                    callback: function (value) {
                        if (value < 0) {
                            return '(' + toPrice(Math.abs(value)).toFormat('0,0.00') + ')'
                        }
                        return toPrice(value).toFormat('0,0.00')
                    },
                    titleClass: 'text-end pe-3',
                    dataClass: 'text-end pe-3',
                    width: '10%',
                },
                {
                    name: 'quantity',
                    title: 'QTY',
                    titleClass: 'text-center',
                    dataClass: 'text-center',
                    width: '5%',
                },
                {
                    name: 'total',
                    title: 'TOTAL',
                    callback: function (value) {
                        if (value < 0) {
                            return '(' + toPrice(Math.abs(value)).toFormat('0,0.00') + ')'
                        }
                        return toPrice(value).toFormat('0,0.00')
                    },
                    titleClass: 'text-end pe-3',
                    dataClass: 'text-end pe-3',
                    width: '10%',
                },
                {
                    name: '__slot:billed_by',
                    title: 'BILLED BY',
                    dataClass: 'ps-3',
                    titleClass: 'ps-3',
                    width: '15%',
                },
                // {
                //   name: '__slot:status',
                //   title: 'STATUS',
                //   dataClass: 'ps-1',
                //   titleClass: 'ps-1',
                //   width: '10%',
                // },
                {
                    name: '__slot:actions',
                    dataClass: 'txt-center',
                    width: '5%',
                },
            ],

            bills: [],

            billToReverse: '',

            form: new window.Form({
                ledger_id: this.invoice.id,
                store_id: null,
                item: null,
                vendor_id: null,
                quantity: 1,
                unit_price: 0,
                date: new Date().toISOString().slice(0, 10),
                comments: '',
                items: [],
            }),

            showStore: false,
            showVendor: false,

            assignedTo: [],

            availableStock: 0,

            enableForm: false,

            reverseForm: new window.Form({
                comments: '',
                detail_id: '',
                ledger_id: this.invoice.id,
                source: '1',
            }),

            selectedBill: '',
        }
    },

    computed: {
        invoiceNotClosed () {
            return this.invoice.processed_at === null
        },

        selectedProduct () {
            if (!this.form.item) {
                return false
            }

            const vm = this

            return this.items.find((item) => item.id === vm.form.item.id)
        },
    },

    watch: {
        'form.item': function (item) {
            this.showStore = false
            this.showVendor = false
            this.assignedTo = []
            this.form.vendor_id = null
            this.form.store_id = null

            if (item) {
                this.setupProductPriceSetup(item)

                this.displayStoreSelection(item)

                this.displayDoctorsAssigned()

                this.findAvailableStock()
            }
        },
        'form.store_id': function (storeId) {
            if (storeId) {
                this.findAvailableStock()
            }
        },
    },

    mounted () {
        this.retrievePatientBills()
    },

    methods: {
        retrievePatientBills () {
            this.$httpClient.get('/patient-billing/patient-bills/' + this.invoice.hashid)
                .then(({ data }) => {
                    this.bills = data.bills.filter(function (bill) {
                        return bill.procedure && bill.department_name !== 'Laboratory'
                    })
                }).catch(() => {
                this.$toast.error('There was a problem retrieving the patient bills')
            })
        },

        openBillingModal () {
            this.$bvModal.show('procedure-bills-modal')
        },

        setupProductPriceSetup (item) {
            if (this.invoice.wallet.type === 0) {
                this.form.unit_price = item.cash_price ? item.cash_price.charge_value * 0.01 : 0
            }

            if (this.invoice.wallet.type === 1) {
                this.form.unit_price = item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0
            }
        },

        chargePatient () {
            if (!this.selectedProduct) {
                this.$toast.error('Please select a product to be charged')
                return
            }

            this.form.items.push({
                item: this.form.item,
                item_id: this.form.item.id,
                quantity: this.form.quantity,
                unit_price: this.form.unit_price,
                vendor_id: this.form.vendor_id,
                store_id: this.form.store_id,
                description: this.form.item.name,
                instruction: null,
            })
            this.form.post('/patient-bills')
                .then(() => {
                    this.$toast.success('The bill has been charged')

                    this.cleanUp()

                    this.$emit('bill-added')
                })
                .catch((error) => {
                    this.form.processing = false
                    this.form.errors.record(error.response.data.errors)
                })
        },

        openReverseForm (rowData) {
            if (rowData.is_payment || rowData.paid) {
                return this.$toast.warning(
                    'You cannot reverse payment / paid bills. Consider using refund',
                )
            }

            this.billToReverse = rowData

            this.reverseForm.detail_id = rowData.id

            this.$bvModal.show('procedure-reverse-modal')
        },

        reverseBills () {
            this.reverseForm.post('/bills/reverse')
                .then(() => {
                    this.$toast.info('Bill deleted successfully')

                    this.$emit('bills:reversed')

                    this.$bvModal.hide('lab-reverse-modal')

                    this.cleanUp()
                })
                .catch((error) => {
                    this.$toast.error('The bill could not be reversed')
                })
        },

        cleanUp () {
            this.billToReverse = ''

            this.retrievePatientBills()
        },

        openResultsModal (bill) {
            this.selectedBill = bill

            if (this.invoice.cash_credit === 0 && bill.total > 0 && bill.paid === false) {
                // this.$toast.error('Please advice the patient to pay for the bill')

                // return
            }

            setTimeout(() => {
                this.$refs.resultsManager.openResultsModal()
            }, 1000)
        },

    },

})
</script>

<template>

    <div>

        <b-modal
            id="procedure-bills-modal"
            body-class="px-4"
            content-class="bg-white rounded-3"
            footer-class="d-flex justify-content-between bg-muted-lt border-top"
            hide-header-close
            modal-class="modal-blur"
            no-close-on-backdrop
            no-close-on-esc
            no-enforce-focus
            title="Billing Form"
            @hidden="cleanUp"
        >
            <div class="form-group mb-4">
                <label class="form-label" for="item_id">Product</label>
                <v-select
                    id="item_id"
                    v-model="form.item"
                    :clearable="false"
                    :options="items"
                    label="name"
                >
                    <template #selected-option="item">
                        <div>
                            <div class="font-weight-medium">
                                {{ item.name }}
                            </div>
                            <div v-if="invoice.cash_credit === 0" class="text-primary small">
                                KES {{ (item.cash_price ? item.cash_price.charge_value * 0.01 : 0) | toMoneyFormat }}
                            </div>
                            <div v-else class="text-primary small">
                                KES {{
                                    (item.scheme_price ? item.scheme_price.charge_value * 0.01 : 0) | toMoneyFormat
                                }}
                            </div>
                        </div>
                    </template>
                </v-select>
                <span
                    v-if="form.errors.has('item_id')"
                    class="text-danger"
                    v-text="form.errors.first('item_id')"
                />
                <p
                    v-if="form.hasError('ledger_id')"
                    class="text-danger"
                    v-text="form.getError('ledger_id')"
                />
            </div>

            <div v-if="form.processing" class="progress mb-2">
                <div class="progress">
                    <div class="progress-bar progress-bar-indeterminate bg-green"/>
                </div>
            </div>

            <template #modal-footer="{cancel}">
                <button :disabled="form.processing" class="btn" type="button" @click.prevent="cancel">
                    Close
                </button>

                <button
                    :disabled="form.processing" class="btn btn-primary"
                    type="submit"
                    @click="chargePatient"
                >
                    Submit
                </button>
            </template>
        </b-modal>

        <b-modal
            id="procedure-reverse-modal"
            body-class="text-center p-4"
            footer-class="d-flex justify-content-center bg-white pt-2"
            header-bg-variant="danger"
            header-text-variant="light"
            hide-header-close
            modal-class="modal-blur"
            no-close-on-backdrop
            no-close-on-esc
            no-enforce-focus
            title="DELETE BILL"
        >
            <div class="mb-4">
                <svg
                    class="icon mb-2 text-danger icon-lg" fill="none" height="24" stroke="currentColor"
                    stroke-linecap="round" stroke-linejoin="round" stroke-width="2" viewBox="0 0 24 24" width="24"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path d="M0 0h24v24H0z" fill="none" stroke="none"/>
                    <path d="M12 9v2m0 4v.01"/>
                    <path
                        d="M5 19h14a2 2 0 0 0 1.84 -2.75l-7.1 -12.25a2 2 0 0 0 -3.5 0l-7.1 12.25a2 2 0 0 0 1.75 2.75"
                    />
                </svg>
                <h3 class="font-weight-bold mb-0 pb-0">
                    Are you sure?
                </h3>
                <div class="text-muted">
                    Do you really want to delete the bill? This action cannot be undone.
                </div>
            </div>

            <form id="reverseForm" @submit.prevent="reverseBills">
                <div class="mb-3">
          <textarea
              v-model="reverseForm.comments"
              class="form-control"
              placeholder="Briefly explain why you are deleting  the bill"
              required
          />
                </div>

                <span
                    v-if="reverseForm.hasError('header_id')"
                    class="text-danger"
                    v-text="reverseForm.getError('header_id')"
                />
            </form>

            <div v-if="reverseForm.processing" class="progress mb-2">
                <div class="progress">
                    <div class="progress-bar progress-bar-indeterminate bg-green"/>
                </div>
            </div>

            <template #modal-footer="{cancel}">
                <button
                    :disabled="reverseForm.processing"
                    class="btn btn-light"
                    @click="cancel"
                >
                    Close
                </button>

                <button
                    :disabled="reverseForm.processing"
                    class="btn btn-danger"
                    form="reverseForm"
                    type="submit"
                    @click="reverseBills"
                >
                    Delete Bill
                </button>
            </template>
        </b-modal>

        <div class="card rounded-3 mb-5 ">
            <div class="card-header">
                <div class="">
                    <h2 class="card-title mb-0 fw-bold">
                        Procedures
                    </h2>
                </div>

                <div class="card-actions">
                    <div class="btn-list">
                        <button v-if="invoiceNotClosed" class="btn btn-primary me-4" @click.prevent="openBillingModal">
                            <i class="uil uil-plus me-1"/> Charge Patient
                        </button>
                    </div>
                </div>
            </div>

            <VueTable
                ref="table"
                :api-mode="false"
                :data="bills"
                :fields="fields"
                :load-on-start="false"
                :show-pagination="false"
                :show-per-page="false"
            >
                <template #date="props">
                    <div class="flex-fill">
                        <div class="ps-3">
                            {{ props.rowData.date | date_DATE_MONTH_YEAR }}
                        </div>
                        <div class="text-indigo small ps-3">
                            {{ props.rowData.date | tIME_HOUR_MINUTES }}
                        </div>
                    </div>
                </template>
                <template #description="props">
                    <div class="flex-fill">
                        <div class="">
                            {{ props.rowData.description }}
                        </div>

                        <div>
              <span
                  v-if="props.rowData.paid && props.rowData.is_payment === false"
                  class="text-success small"
              >Paid</span>
                            <span
                                v-if="props.rowData.paid && props.rowData.is_payment && props.rowData.instructions !== 'REFUND'"
                                class="text-success small"
                            >Payment</span>
                            <span
                                v-if="props.rowData.paid && props.rowData.is_payment && props.rowData.instructions === 'REFUND'"
                                class="text-purple small"
                            >Refund</span>
                            <span
                                v-if="!props.rowData.paid && Number.parseFloat(props.rowData.unit_price) > 0 && props.rowData.is_discount === false"
                                class="text-danger small"
                            >Unpaid</span>
                        </div>

                        <div v-if="props.rowData.result_done" class="col-auto align-self-center">
                            <div class="badge bg-success badge-pill"/>
                            <small>Result entered</small>
                        </div>
                    </div>
                </template>
                <template #billed_by="props">
                    <div class="d-flex align-items-center">
                        <div class="flex-fill">
                            <div class="font-weight-medium">
                                {{ props.rowData.billed_by }}
                            </div>
                            <small class="text-muted text-truncate small">
                                {{ props.rowData.user_department }}
                            </small>
                        </div>
                    </div>
                </template>
                <template #status="props">
                    <div>
            <span
                v-if="props.rowData.paid && props.rowData.is_payment === false"
                class="badge bg-teal-lt badge-pill"
            >Paid</span>
                        <span v-if="props.rowData.paid && props.rowData.is_payment" class="badge bg-teal-lt badge-pill">Payment</span>
                        <span
                            v-if="!props.rowData.paid && Number.parseFloat(props.rowData.unit_price) > 0"
                            class="badge badge-pill bg-danger"
                        >Unpaid</span>
                    </div>
                </template>
                <template #actions="props">
                    <div class="dropdown">
                        <button
                            class="btn align-text-center py-1"
                            data-bs-toggle="dropdown"
                        >
                            <i class="uil uil-ellipsis-h"/>
                        </button>
                        <div class="dropdown-menu dropdown-menu-end">
                            <div v-if="props.rowData.templates_count > 0">
                                <a
                                    class="dropdown-item"
                                    href="#"
                                    @click.prevent="openResultsModal(props.rowData)"
                                >
                                    <i class="uil uil-stethoscope me-2 fs-2"/> Results Management
                                </a>

                                <a
                                    v-if="props.rowData.result_done"
                                    class="dropdown-item"
                                    href="#"
                                    @click.prevent="approveResults(props.rowData)"
                                >
                                    <i class="uil uil-check me-2 fs-2"/> Approve
                                </a>
                            </div>

                            <div class="dropdown-divider"/>

                            <a
                                v-if="!props.rowData.is_payment && !props.rowData.paid && !props.rowData.done && !invoiceProcessed"
                                v-can="'delete-patient-bill'"
                                class="dropdown-item text-danger"
                                href="#"
                                @click.prevent="openReverseForm(props.rowData)"
                            >
                                <i class="uil uil-trash-alt me-2 fs-2"/> Delete
                            </a>
                        </div>
                    </div>
                </template>
            </VueTable>
        </div>

    </div>
</template>

<style scoped>

</style>
